/* BASIC -------------------------------------------------------------------- */
* {
  box-sizing: border-box;
}

body {
  background-color: rgb(229, 229, 203);
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
}

main {
  display: block;
  height: 100%;
}

.light {
color: rgb(255, 249, 236);
}

/* HEADER -----------------------------------------------------------------*/

body > header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  background-color: rgb(95, 141, 78);
  padding: 0 1rem 0 1rem;
  height: 4rem;
}

#nav-icon {
  margin-top: .8rem;
  width: 2.2rem;
  height: 2.2rem;
  border: solid;
  border-radius: 10px;
  padding: 0.3rem;
  border-color: rgb(255, 249, 236);
}


h1 {
  font-family: 'Unbounded', cursive;
  /* color: rgb(229, 229, 203); */
  color: rgb(255, 249, 236);
  margin-top: .7rem;
}

.brand-font {
  font-family: 'Unbounded', cursive;
}

/* NAV BAR ------------------------------------------------------------------ */

nav {
  display: block;
}

nav > ul {
  background-color: #285430;
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-width: 2px 0 2px 0;
  border-style: solid;
  border-color: rgb(255, 249, 236);
}

nav li {
  border-bottom: 2px solid rgb(255, 249, 236);
}

nav li:last-child {
  border-bottom: none;
}

nav a {
  display: block;
  text-align: center;
  padding: 1rem;
  color: rgb(255, 249, 236);
  text-decoration: none; /*gets rid of underline*/
}

nav a:active {
  background-color:rgb(95, 141, 78);
}

.current-page {
  color: rgb(163, 203, 100);
}

/* INTRODUCTION --------------------------------------------------------------*/

#introduction p {
  color: rgb(26, 18, 11); /*dark dark brown*/
  margin: 0 1rem 1rem 1rem;
}

#introduction {
  margin: 1.7rem;
}

h2 {
  color: rgb(95, 141, 78); /*green*/
  font-family: 'Unbounded', cursive;
  margin: 1rem 0;
  text-align: center;

}

h4 {
  color: rgb(95, 141, 78); /*green*/
  font-family: 'Unbounded', cursive;
  margin: 1rem 0;
  text-align: center;

}

.highlight {
  background-color: rgb(255, 249, 236);
}

/* GALLERY ------------------------------------------------------------------ */

.gallery-card-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(95, 141, 78);
  padding: 1.7rem;
}

/* .gallery {
 
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;

} */

.gallery-card {
  background-color: rgb(255, 249, 236);
  margin: 1.7rem 1.7rem 0 1.7rem;
  width: 100%;
  padding: 1.7rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  border-radius: 0 23px 0 23px;
  max-width: 30rem;
  min-width: 15rem;
  box-shadow: 3px 3px 3px 0 rgb(26, 18, 11);
}


.gallery-card-title {
  font-family: 'Unbounded', cursive;
  color:rgb(26, 18, 11);
  text-align: center;
  padding: 1rem 0 0 0;
  margin: 0 1.5rem;
  text-decoration: underline;
  font-size: 2rem;
}

.gallery-card-artist {
  text-align: center;
  padding: .4rem 0 0 0;
  margin: 0 1rem 0 1rem;
  color: rgb(95, 141, 78);
}
/* .gallery-card-artwork {
  text-align: center;
  padding: .4rem 0 0 0;
  margin: 0 1rem 0 1rem;
  color: rgb(95, 141, 78);
} */

.gallery-card img {
  width: 80%;
  padding: .5rem;
  background-color:rgb(229, 229, 203);
  border: 1px solid rgb(26, 18, 11);
  border-radius: 3px;
  box-shadow: 12px 12px 2px 1px rgba(0, 0, 255, .2);
  align-items: center;
  height: auto;
  text-align: center;
  margin: 0 2rem 1rem 1.7rem;
}

/* PROFILE ------------------------------------------------------------------ */

#photo-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(169, 196, 127);
  width: 100%;
}

.dark-green-background {
  background-color: rgb(95, 141, 78);
}

.user-info {
  background-color: rgb(255, 249, 236);
  width: 100%;
  border-style: solid;
  border-width: 20px 0 20px 0;
  border-color:#285430;
}

.user-info h2 {
  text-align: center;
  border-radius: 2px;
  border-style: solid;
  border-color: rgb(95, 141, 78);
  padding: 1rem 0;
  margin: 1rem 2rem 0 2rem;
}

#contact-info {
  list-style-type: none;
}

#photo {
  margin-bottom: 1.7rem;
}

#saved-art{
  background-color: rgb(95, 141, 78);
  padding: 0 2rem 1rem 2rem;
}

.saved-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.saved-artwork {
  width: 40%;
  max-width: 20rem;
  min-width: 17rem;
  margin: 0 .5rem 1rem .5rem;
  border-radius: 15px 0 15px 0;
  border: 2px solid rgb(255, 249, 236);
  box-shadow: -5px -5px 0 0 rgb(163, 203, 100),
  5px 5px 0 0 rgb(26, 18, 11);
}

#saved-art h2 {
  margin: 1rem 0 2rem 0;
  padding: 1rem 0;
  color:rgb(255, 249, 236);
  text-align: center;
  border-radius: 22px;
  border-style: solid;
  border-color:rgb(255, 249, 236);
}

/* SEARCH ART------------------------------------------------------------------- */

input[type=text] {
  width: 150px;
  transition: width 0.5s ease-in-out;
}

/* When the input field gets focus, change its width to 80% */
input[type=text]:focus {
  width: 80%;
}

section.main-filter {
  margin: 0;
  height: 40rem;
  background-color:rgb(255, 249, 236)
}

.filter {
  margin-top: 0;
  background-color: rgb(255, 249, 236);
  text-align: center;
  display: block;
}

#medium-container button {
  background-color: #285430; /* Green */
  border: none;
  color: white;
  padding: .5rem .7rem 1.6rem .7rem;
  height: 2rem;
  border-radius: 15px;
  text-align: center;
  text-decoration: none;
  margin: .2rem .2rem;
  font-family: 'Unbounded', cursive;
}

#medium-container button:hover {
  background-color: rgba(149, 225, 121, 0.614);
  color: rgb(84, 125, 69);
}

#medium-container,
#seller-container {
  display: inline-flex;
  width: 100%;
  min-width: 17rem;
  max-height: 15rem;
  min-height: 7rem;
  flex-wrap: wrap;
  padding: 1.7rem 1.7rem 0rem 1.7rem;
  justify-content: flex-start;
  text-align: center;
}

#filtered-art,
#filtered-artists {
  height: 30rem;
  margin: 1rem 1.7rem;
  border-radius: 15px;
  display: block;
  padding: 0 2rem 1rem 2rem;

}

#filtered-art {
  background-color: rgb(169, 196, 127); /*light green*/
}

.img-fluid {
  height:300px;
  width:300px;
}

/* SELLER --------------------------------------------------------------------- */

h1.color-change {
  color: #285430;
}

#seller-container button {
  background-color: #285430;

  border: none;
  color: white;
  padding: .5rem .7rem 1.6rem .7rem;
  height: 2rem;
  border-radius: 15px;
  text-align: center;
  text-decoration: none;
  margin: .2rem .2rem;
  font-family: 'Unbounded', cursive;
}

#seller h4 {
  color: rgb(125, 182, 104); /*green*/
  font-family: 'Unbounded', cursive;
  margin: 1rem 0;
  text-align: center;

}

#seller-container button:hover {
  background-color: rgba(149, 225, 121, 0.614);
  color: rgb(84, 125, 69);
}

.filter-title {
  margin: 0;
  padding: 1.7rem 0 0 0;
  text-align: center;
}

.artist {
  color: rgb(241, 190, 39);
}


 .artist {
  color: white;
} 

.art {
  color: rgb(125, 182, 104);
}

.line-artist {
  text-decoration: underline;
  color: #285430;
}

.line-art {
  text-decoration: underline;
  color: #285430;
}

.filter-title.art {
  text-align: center;
}

/* Location filter buttons and background-------------------------------------------------------------------- */

.btn.filter-btn-loc {
  background-color: rgb(255, 208, 66);
  border: none;
  color: white;
  padding: .5rem .7rem 1.6rem .7rem;
  height: 2rem;
  border-radius: 15px;
  text-align: center;
  text-decoration: none;
  margin: .2rem .2rem;
  font-family: 'Unbounded', cursive;
  justify-content: center;
}


.btn.filter-btn-loc button:hover {
  background-color: rgb(255, 218, 107);
  color: rgb(182, 137, 5);
}


.LocationFiler {
  background-color: #285430;
}

