/* FOOTER -------------------------------------------------------------------- */
footer h1 {
  background-color: #285430;
  margin: -1rem 0;
}

footer {
  background-color: #285430;
  padding: 2rem;
}

footer {
  color:rgba(255, 249, 236, 0.497);
}

footer h1 {
  text-align: center;
  padding-top: 2rem;
  color:rgba(255, 249, 236, 0.256);
}

.footer-desc {
  margin: 2rem 3.5rem 0 4rem;
  padding-bottom: 2rem;
  display: flex;
  justify-content: flex-start;
  align-content: flex-start;
  flex-direction: column;
}

section.footer-desc p {
  display: block;
  font-size: .8rem;
}

.gallery {
  padding-bottom: 2.7rem;
}

/* SECOND MEDIA QUERY --------------------------------------------------------*/

@media (min-width: 768px) {
  #hamburger-menu {
    display: none;
  }

  nav > ul {
    display: flex;
    border: none;
  }

  nav li {
    border: none;
  }

  .current-tab-light {
    border-bottom: 2px solid rgb(255, 249, 236);
  }

  nav li:last-child.current-tab-light {
    border-bottom: 2px solid rgb(255, 249, 236);
  }

 .gallery-card-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 1.7rem;
    padding-bottom: 4.4rem;
  }

  .gallery-card {
    width: 12rem;
    margin: 2rem .7rem 0 .7rem;
  }

  .gallery-card-title {
    font-size: 1.5rem;
  }

  .gallery-card img {
    width: 75%;
  }

  #profile-section section.gallery-card {
    box-shadow: 3px 3px 7px 0 rgba(26, 18, 11, 0.375);
    border: 3px solid #285430;
  }

  #photo-info {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: rgb(169, 196, 127);
    width: 100%;
  }

  .user-info {
    border-width: 3px;
    border-radius: 15px;
    margin-right: 1.7rem;
    box-shadow: 3px 3px 7px 0 rgba(26, 18, 11, 0.375);
  }

  .saved-artwork {
    max-width: 12rem;
    min-width: 12rem;
    margin: 0 .5rem 1rem .5rem;
    box-shadow: 5px 5px 0 0 rgb(26, 18, 11);
  }

  #saved-art {
    padding: 1.7rem 2rem 2.7rem 2rem;
  }

  #saved-art > h2 {
    background-color: #285430;
  }

  .footer-desc {
    margin: 2rem 12rem 0 12rem;
  }
}


